.myItem {
  padding: 0;
  word-wrap: break-word;
  white-space: pre-wrap; }

.Favorites {
  text-align: center;
  border: solid red;
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 90%; }

.button {
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: row;
  margin-top: 30px;
  padding: 0; }

.card {
  width: 95%;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  margin: 7px;
  background-color: white;

  border-radius: 10px; }

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2); }

.list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 0 0 0;
  padding: 0;
  word-wrap: break-word; }




